import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import ('../components/FrontPage.vue')
  },
  {
    path: '/mindmapinput',
    name: 'mindmapinput',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../components/MindmapInput.vue')
  },
  {
    path: '/mindmapresult',
    name: 'mindmapresult',
    component: () => import('../components/MindmapResult.vue')
  },
  {
    path: '/mcdcinput',
    name: 'mcdcinput',
    component: () => import('../components/MCDCInput.vue')
  },  {
    path: '/mcdcresult',
    name: 'mcdcresult',
    component: () => import('../components/MCDCResult.vue')
  },
  {
    path: '/errorpage',
    name: 'errorpage',
    component: () => import('../components/errorPage.vue')
  },
  
]

const router = new VueRouter({
  //mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
